.Button {
  background-color: transparent;
  border: none;
  color: white;
  outline: none;
  cursor: pointer;
  font: inherit;
  padding: 10px;
  margin: 10px;
  font-weight: bold;
}

.Success {
  color: #5c9210;
}

.Danger {
  color: #944317;
}
.Button.Success:hover {
  background-color: #5c9210;
  color: white;
}

.Button.Danger:hover {
  background-color: #944317;
  color: white;
}
